import { Navbar } from "flowbite-react";
import { NavbarLinkProps } from "flowbite-react/lib/esm/components/Navbar/NavbarLink";
import React from "react";
import { PrimaryButton } from "../../";

function NavLink(props: NavbarLinkProps) {
  //   const location = useLocation();

  return (
    <Navbar.Link
      href={props.href}
      active={props.active}
      className={"dark:hover:text-secondery md:dark:hover:text-secondery lg:dark:hover:text-secondery".concat(
        ` ${props.className ? props.className : ""}`
      )}
    >
      <span className="">{props.children}</span>
    </Navbar.Link>
  );
}

function NavBar({}) {
  return (
    <Navbar
      fluid={false}
      rounded={false}
      className="fixed bg-white dark:dark:bg-primary lg:bg-primary md:dark:bg-primary w-screen z-50"
    >
      <Navbar.Brand href="/">
        <img
          src="logo.png"
          className="mr-3 h-6 sm:h-9 rounded-full"
          alt="Flowbite Logo"
        />
        <span className="self-center whitespace-nowrap text-xl font-semibold dark:text-secondery">
          RAMESH
        </span>
      </Navbar.Brand>
      <div className="flex md:order-2 content-center">
        <a
          href="./Resume-Ramesh-Shyaman.pdf"
          target={"_blank"}
          rel={"noreferrer"}
          className="w-full h-full"
        >
          <PrimaryButton>Resume</PrimaryButton>
        </a>
        <Navbar.Toggle className="mx-5" />
      </div>
      <Navbar.Collapse>
        <NavLink href="/">Home</NavLink>
        <NavLink href="/">Skills</NavLink>
        <NavLink href="/">Projects</NavLink>
        <NavLink href="/">About Me</NavLink>
      </Navbar.Collapse>
    </Navbar>
  );
}

export default NavBar;
